}

.hero-description {
  color: var(--white-color);
  letter-spacing: -.02em;
  max-width: 248px;
  margin-top: 40px;
  margin-left: auto;
  font-size: 14px;
  font-weight: 300;
  line-height: 128.571%;
}

.hero-description-and-carousel-container {
  flex-direction: column-reverse;
  margin-bottom: 40px;
  display: flex;
  position: relative;
}

.card-carousel {
  perspective: 2000px;
  perspective-origin: top;
  width: 266px;
  height: 252px;
  margin: 0 auto;
  padding: 0;
  position: relative;
  overflow: hidden;
}

.card-carousel .inner-carousel {
  background-color: var(--dark-green-color);
  transform-style: preserve-3d;
  width: 100%;
  margin: 0 auto;
  position: relative;
  top: 0;
}

.card-carousel .inner-carousel > div {
  opacity: 1;
  background-color: var(--dark-green-color);
  z-index: 1;
  justify-content: center;
  align-items: center;
  width: 266px;
  height: 200px;
  margin: 0 auto;
  padding: 5px 20px;
  transition: all .5s ease-out;
  display: flex;
  position: absolute;
}

.card-carousel .inner-carousel > div:after {
  content: "";
  opacity: .8;
  background-color: var(--dark-green-color);
  z-index: 1;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.card-carousel .inner-carousel > div.front {
  background-color: var(--dark-green-color);
}

.card-carousel .inner-carousel > div.counterclockwise:after, .card-carousel .inner-carousel > div.clockwise:after {
  opacity: .4;
  cursor: pointer;
}

.card-carousel .inner-carousel > div.front:after {
  content: none;
}

.carousel-image-card-presentation {
  justify-content: space-between;
  width: 100%;
  max-width: 436px;
  margin-bottom: 40px;
  display: flex;
  position: absolute;
  top: calc(100% - 32px);
}

.button-spin {
  border: 1px solid var(--white-transparent-color);
  cursor: pointer;
  background: none;
  border-radius: 8px;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  display: inline-flex;
}

.left {
  margin-right: 12px;
}

.right {
  margin-right: auto;
}

.button-spin:hover, .button-spin:focus {
  border-color: var(--white-color);
}

.hero-carousel-image-card-button-svg {
  stroke: var(--white-transparent-color);
  width: 18px;
  height: 18px;
}

.hero-carousel-image-card-button-svg:hover, .hero-carousel-image-card-button-svg:focus {
  stroke: var(--white-color);
}

.hero-carousel-image-card-buttons-numbers {
