  letter-spacing: -.02em;
  font-size: 32px;
  font-weight: 600;
  line-height: 125%;
}

.catalog-cards-list {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 40px;
  padding: 0;
  display: flex;
}

.catalog-cards-list-item {
  flex-direction: column;
  align-items: flex-start;
  gap: 14px;
  width: 100%;
  display: flex;
}

.catalog-cards-list-item-image-container {
  justify-content: center;
  align-items: center;
  width: 100%;
  max-height: 360px;
  padding: 0;
  display: flex;
  position: relative;
}

.flip-container {
  perspective: 1000000px;
  perspective-origin: top;
  cursor: pointer;
  transform-style: preserve-3d;
}

.back {
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-left: auto;
  display: flex;
  transform: rotateY(90deg);
}

.front {
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 43px 30px;
  display: flex;
  position: absolute;
}

.front > svg {
  z-index: 999;
  stroke: var(--black-color);
  stroke-opacity: .1;
  display: none;
  position: fixed;
  top: 14px;
  right: 14px;
}

.flip-container:hover .front, .flip-container:focus .front {
  animation: .25s ease-in-out forwards flip;
}

.flip-container:hover .back, .flip-container:focus .back {
  animation: .25s ease-out .25s reverse forwards flip;
}

@keyframes flip {
  0% {
    transform: rotateY(0);
  }

  100% {
    transform: rotateY(-90deg);
  }
}

